'use strict';

define(['knockout', 'text!./view.html', 'app/data-context-code', 'lib/knockout-bindings-datetime'], function (ko, template, dataCtx) {

    "use strict";

    function ViewModel() {

        var self = this;

        self.repos = ko.observableArray([]);
        self.loading = ko.observable(true);
        self.error = ko.observable(false);
        self.hasRepos = ko.pureComputed(function () {

            return this.repos().length > 0;
        }, this);

        dataCtx.repos.get(100).then(function (repos) {

            self.repos(repos);
            self.loading(false);
        })['catch'](function (err) {

            self.loading(false);
            self.error(true);

            console.log('Error occured trying to get the repositories', err);
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});